import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Charts from "../views/Charts.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/charts",
    name: "Charts",
    component: Charts,
  },
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

export default router;
